.wysiwyg p,
.wysiwyg ul,
.wysiwyg ol {
  font-size: 17px;
}
.wysiwyg p {
  line-height: 1.8;
  margin: 28px 0 14px;
}
.wysiwyg a {
  text-decoration: underline;
}
.wysiwyg ul {
  list-style: none;
  margin: 14px 0;
  padding: 0;
}
.wysiwyg ul li {
  position: relative;
  padding-left: 31px;
}
.wysiwyg ul li:not(:last-of-type) {
  margin-bottom: 20px;
}
.wysiwyg ul li:before {
  content: '';
  position: absolute;
  left: 0;
  top: 10px;
  width: 16px;
  height: 2px;
  background-color: #b0c468;
