.intro {
  position: relative;
}
.intro .slider .slide {
  position: relative;
  background-color: #36596e;
}
.intro .slider .slide__wrap {
  display: flex;
}
.intro .slider .slide .text {
  color: #fff;
  width: calc(50% - 167px);
  padding: 55px 70px 70px 0;
  height: 450px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}
.intro .slider .slide .text .name {
  font-size: 45px;
  font-weight: 700;
  margin: 0;
  margin-bottom: 16px;
}
.intro .slider .slide .text p {
  font-size: 15px;
  margin: 0;
  margin-bottom: 21px;
  line-height: 1.7;
}
.intro .slider .slide .text .link {
  color: #b0c468;
  font-size: 15px;
  font-weight: 500;
}
.intro .slider .slide .image {
  position: absolute;
  right: 0;
  top: 0;
  width: calc(50% + 167px);
  height: 100%;
}
.intro .slider .slide .image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.intro .navigate {
  position: absolute;
  left: 0;
  bottom: 37px;
  width: 100%;
}
.intro .navigate__wrap {
  display: flex;
  align-items: center;
}
.intro .navigate .arrow {
  cursor: pointer;
}
.intro .navigate .arrow svg {
  width: 15px;
  height: 16px;
  fill: #fff;
}
.intro .navigate .arrow.prev {
  transform: rotate(-90deg);
}
.intro .navigate .arrow.next {
  transform: rotate(90deg);
}
.intro .navigate .dots {
  margin: 0 24px;
}
.intro .navigate .dots ul {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  align-items: center;
}
.intro .navigate .dots li {
  position: relative;
  width: 14px;
  height: 14px;
  margin: 0 8px;
  cursor: pointer;
}
.intro .navigate .dots li .dots-svg {
  opacity: 0;
  transition: 0.3s;
  outline: none;
}
.intro .navigate .dots li .dots-svg #bar {
  stroke-dashoffset: 0;
  stroke-linecap: round;
}
.intro .navigate .dots li span {
  position: absolute;
  background-color: #fff;
  width: 4px;
  height: 4px;
  display: block;
  top: 5px;
  left: 5px;
  border-radius: 50px;
  transition: 0.3s;
  opacity: 1;
}
.intro .navigate .dots li.slick-active span {
  opacity: 0;
  transition: 0.3s;
}
.intro .navigate .dots li.slick-active .dots-svg {
  opacity: 1;
}
.intro .navigate .dots li.slick-active .dots-svg #bar {
  animation: circle 6s linear forwards;
  stroke-dashoffset: 0;
}
.intro .navigate .count {
  font-size: 12px;
